"use client";

import React from "react";

interface ExamQuestionProps {
  question: {
    question: string;
    options: string[];
    correctAnswer: string;
  };
  onAnswer: (answer: string) => void;
  questionNumber: number;
}

const ExamQuestion: React.FC<ExamQuestionProps> = ({ question, onAnswer, questionNumber }) => {
  if (!question) return <p className="text-red-500 text-center">Error: Pregunta no disponible.</p>;

  return (
    <div className="p-6 bg-white shadow-md rounded-lg">
      <h2 className="text-xl font-semibold mb-4">
        {questionNumber}. {question.question}
      </h2>

      <div className="space-y-2">
        {question.options.map((option, index) => (
          <button
            key={`${question.question}-${index}`} // ðŸ”¹ Usa una key Ãºnica para cada opciÃ³n
            onClick={() => onAnswer(option)}
            className="block w-full p-3 bg-gray-200 rounded-lg hover:bg-gray-300 transition"
          >
            {option}
          </button>
        ))}
      </div>
    </div>
  );
};

export default ExamQuestion;
